@mixin loop-size($counter) {
  @while $counter > 0 {
    &[data-bvi-fontsize='#{$counter}'] {
      font-size: (0px + $counter) !important;
    }

    &[data-bvi-fontsize='#{$counter}'] {
      h1,
      h1 * {
        font-weight: bold !important;
        font-size: (7px + $counter) !important;
      }

      h2,
      h2 * {
        font-weight: bold !important;
        font-size: (6px + $counter) !important;
      }

      h3,
      h3 * {
        font-weight: bold !important;
        font-size: (5px + $counter) !important;
      }

      h4,
      h4 * {
        font-weight: bold !important;
        font-size: (4px + $counter) !important;
      }

      h5,
      h5 * {
        font-weight: bold !important;
        font-size: (3px + $counter) !important;
      }

      h6,
      h6 * {
        font-weight: bold !important;
        font-size: (2px + $counter) !important;
      }
    }

    $counter: $counter - 1;
  }
}

@mixin loop-theme($name, $bg, $color) {
  &[data-bvi-theme='#{$name}']:not(.bvi-no-styles) {
    background-color: $bg !important;
    background: $bg !important;
    background-image: none !important;
    box-shadow: none !important;
    text-shadow: none !important;
    border-color: $color !important;
    color: $color !important;
    fill: $color !important;

    &[data-bvi-images='off']:not(.bvi-no-styles) {
      .bvi-background-image,
      img.bvi-img {
        &::before {
          border: 1px dashed $color;
        }
      }
    }

    :checked,
    :default,
    :disabled,
    :empty,
    :enabled,
    :first-child,
    :first-of-type,
    :focus,
    :indeterminate,
    :hover,
    :last-child,
    :last-of-type,
    :link,
    :nth-last-child,
    :nth-last-of-type,
    :nth-of-type,
    :only-child,
    :only-of-type,
    :optional,
    :read-write,
    :required,
    :root,
    :target,
    :valid,
    :visited,
    :first-child:first-letter,
    :default {
      background-color: $bg !important;
      box-shadow: none !important;
      text-shadow: none !important;
      border-color: $color !important;
      color: $color !important;
      fill: $color !important;
    }

    &:after {
      background-color: rgba(255, 255, 255, 0) !important;
      color: $color !important;
      box-shadow: none !important;
      text-shadow: none !important;
      border-color: transparent !important;
    }

    &:before {
      background-color: rgba(255, 255, 255, 0) !important;
      color: $color !important;
      box-shadow: none !important;
      text-shadow: none !important;
      border-color: transparent !important;
    }

    &::selection {
      background-color: $color !important;
      color: $bg !important;
    }

    .bvi-speech-btn,
    .bvi-speech-text {
      border-color: $color;
    }

    a {
      text-decoration: underline !important;

      &.bvi-btn {
        border-color: $color;
        color: $color !important;
        text-decoration: none !important;

        &.disabled {
          color: rgba(red($color), green($color), blue($color), 0.5) !important;
          border: 1px solid rgba(red($color), green($color), blue($color), 0.5) !important;
          pointer-events: none;
          cursor: no-drop;
          text-decoration: none !important;

          &:focus {
            color: rgba(red($color), green($color), blue($color), 0.5) !important;
            border: 1px solid rgba(red($color), green($color), blue($color), 0.5) !important;
            text-underline: none !important;
            text-decoration: none !important;
          }
        }

        &:hover {
          color: $bg !important;
          background-color: $color !important;
          text-underline: none !important;
          text-decoration: none !important;
        }

        &:focus {
          color: $color !important;
          background-color: $bg !important;
          text-underline: none !important;
          text-decoration: none !important;
        }
      }

      outline: 0 !important;
    }

    & a {
      outline: 0 !important;
      color: $color !important;
      background-color: transparent !important;
      text-decoration: underline !important;

      &.active,
      &.show {
        color: $color !important;
        background-color: $bg !important;
        text-decoration: none !important;
      }

      &:hover {
        color: $bg !important;
        background-color: $color !important;
        text-decoration: none !important;

        &:after {
          background-color: rgba(255, 255, 255, 0) !important;
          color: $bg !important;
          box-shadow: none !important;
          text-shadow: none !important;
          border-color: transparent !important;
        }

        &:before {
          background-color: rgba(255, 255, 255, 0) !important;
          color: $bg !important;
          box-shadow: none !important;
          text-shadow: none !important;
          border-color: transparent !important;
        }
      }

      &:hover * {
        color: $bg !important;
        background-color: $color !important;
        text-decoration: none !important;

        &:after {
          background-color: rgba(255, 255, 255, 0) !important;
          color: $bg !important;
          box-shadow: none !important;
          text-shadow: none !important;
          border-color: transparent !important;
        }

        &:before {
          background-color: rgba(255, 255, 255, 0) !important;
          color: $bg !important;
          box-shadow: none !important;
          text-shadow: none !important;
          border-color: transparent !important;
        }
      }
    }

    & button,
    & input[type="submit"],
    & input[type="button"],
    & button[type="submit"],
    & button[type="button"] {
      color: $bg !important;
      background-color: $color !important;
      text-decoration: none !important;
    }

    & button:hover,
    & input[type="button"]:hover,
    & input[type="submit"]:hover,
    & button[type="submit"]:hover,
    & button[type="button"]:hover {
      &.active {
        color: $bg !important;
        background-color: $color !important;
        text-decoration: none !important;
      }

      &:hover {
        color: $bg !important;
        //background: $color !important;
        background-color: $color !important;
        text-decoration: none !important;
      }

      &:hover * {
        color: $bg !important;
        background-color: $color !important;
        text-decoration: none !important;
      }
    }
  }
}
